import Modal from "react-bootstrap/Modal";
import Button from "react-bootstrap/Button";
import { useState } from "react";
import ModalHeader from "react-bootstrap/ModalHeader";
import ModalTitle from "react-bootstrap/ModalTitle";

function Modal2() {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <>
      <button className="button" onClick={handleShow}>
        Call Script
      </button>
      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Make a Call!</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          Hello, I’m [NAME], a constituent calling from [CITY, ZIP] and a
          [student, scientist, parent, etc. -- how do you identify?] from [org,
          institution, etc.]. I’m extremely concerned about speeding up our
          clean energy transition because [YOUR WHY]. I’m calling to urge
          [Senator/Representative NAME] to please support the extension of clean
          energy tax credits for wind, solar and energy efficiency, and
          establish new tax credits for energy storage projects. Please pass
          along my concerns to the [Senator/Representative]. I’d appreciate it
          if your office could follow up with me with any news related to this
          issue. I can be reached at [CONTACT INFO]. Thanks so much for taking
          my call.
        </Modal.Body>
        <Modal.Footer>
          <button className="button" variant="secondary" onClick={handleClose}>
            Close
          </button>
        </Modal.Footer>
      </Modal>
    </>
  );
}

export default Modal2;
