import React, { useState } from "react";
import Axios from "axios";
import { Modal, Button } from "react-bootstrap";

const FirstCall = (props) => {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const clickHandler = () => {
    Axios.post(
      `https://climate-changemakers.herokuapp.com/api/calls/make/${props.identifier}`
    ).catch((error) => {
      console.error(error);
    });
  };
  return (
    <>
      <button
        className="button card_button"
        variant="primary"
        onClick={handleShow}
      >
        {props.dcNumber}
      </button>
      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>Call your Representatives</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="phone_script_header">
            Get ready to call your rep! Once you click, you will be immediately
            directed to the call so take a second to prepare what you want to
            say. Feel free to use this script below:
          </div>
          <div className="phone_script">
            Hello, I’m [NAME], a constituent calling from [CITY, ZIP] and a
            [student, scientist, parent, etc. -- how do you identify?] from
            [org, institution, etc.]. I’m extremely concerned about speeding up
            our clean energy transition because [YOUR WHY]. I’m calling to urge
            [Senator/Representative NAME] to please support the extension of
            clean energy tax credits for wind, solar and energy efficiency, and
            establish new tax credits for energy storage projects. Please pass
            along my concerns to the [Senator/Representative]. I’d appreciate it
            if your office could follow up with me with any news related to this
            issue. I can be reached at [CONTACT INFO]. Thanks so much for taking
            my call.
          </div>
        </Modal.Body>
        <Modal.Footer>
          <button className="button" variant="primary" onClick={clickHandler}>
            Make the Call!
          </button>
        </Modal.Footer>
      </Modal>
    </>
  );
};

export default FirstCall;
